<template>
  <div class="py-5">
    <div class="container">
      <b-card
        :title="image.title"
        :sub-title="(image.city?image.city+' ':'')+image.country"
        :img-src="image.largePath"
        img-top
        class="mb-2"
      >
        <b-card-text>{{image.description}}</b-card-text>
        <b-button
          variant="outline-success"
          :class="{active: isFavor(image.id)}"
          @click="isFavor(image.id)?unlike(image.id):like(image.id)"
        >{{image.favorCount}} Like</b-button>
      </b-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      image: {}
    };
  },
  computed: {
    favorList() {
      return this.$store.getters.getFavorList;
    }
  },
  created() {
    this.getImage();
  },
  methods: {
    getImage() {
      axios
        .get("../api/GetImage.php", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(response => {
          this.image = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    isFavor(id) {
      return this.favorList.includes(id + "");
    },
    like(imageid) {
      if (this.$store.getters.isLogin) {
        this.$store
          .dispatch("like", { imageid: imageid })
          .then(this.getImage)
          .catch(error => console.log(error));
      } else {
        this.$router.push({ name: "login" });
      }
    },
    unlike(imageid) {
      this.$store
        .dispatch("unlike", { imageid: imageid })
        .then(this.getImage)
        .catch(error => console.log(error));
    }
  }
};
</script>

